import React, {useState} from 'react'
import './movies.css'
import Data from "./data/db.json"
import Button from '@material-ui/core/Button'

import videoComponent from './assets/god1.mp4'






function Webserese() {
    const [searchTerm, setSearchTerm] = useState("");
    
    
    return (
        <div className="main__container">

        <div className="video__autoplay">
            <video autoPlay loop muted>
                <source src={videoComponent} type="video/m" />
            </video>
            <h1 className="header__header" >Godzila vs Kong </h1>
            
            
        </div>
        <div className="search_container">
        <input type="text" placeholder="search web sereases" onChange={event => {
            setSearchTerm(event.target.value);
        }}
         ></input>
        
        
        </div>

        <div className="movie__background">

        <div className="items">
        
            { Data.filter((val)=>{
                if (searchTerm === ""){
                    return val
                }else if (val.name.toLowerCase().includes(searchTerm.toLowerCase())){
                    return val
                }else {
                    return('')
                }
            }).map(post => {
                return (
                    
                    
                    <a href={post.id} ><div  className="item"><img  className="img-responsive" src={post.image} alt="movie_images"/></div></a>
                    
                    
                    
                    
                    
                )
            })}
            

        </div>
        </div>
        
        

        </div>
    )
}

export default Webserese;